.chefs {
  &__title {
    position: relative;
    margin: 0 auto;
    margin-bottom: 80px;
    &::before,
    &::after {
      position: absolute;
      bottom: 0;
      content: '';
      width: 352px;
      height: 2px;
      background-color: var(--color-secondary-light);
    }
    &--color {
      color: var(--color-primary-accent);
    }
    &::before {
      display: none;
      @media screen and (min-width: $tablet) {
        display: block;
        left: 0;
      }
    }
    &::after {
      display: none;
      @media screen and (min-width: $tablet) {
        display: block;
        right: 0;
      }
    }
  }
}

.chefs-list {
  @media screen and (min-width: $tablet) {
    @include centeredFlex();
    gap: 30px;
  }
  &__item {
    @include centeredFlex($align: flex-start);
    gap: 30px;
    &:not(:last-child) {
      margin-bottom: 35px;
      @media screen and (min-width: $tablet) {
        margin-bottom: 0;
      }
    }
  }
  &__img {
    width: 156px;
    height: 258px;
    border-radius: 4px 4px 4px 200px;
    box-shadow: -16px 16px 0 0 var(--color-primary-light),
      -16px 16px 0 2px var(--color-secondary-dark);
    @media screen and (min-width: $tablet) {
      width: 268px;
      height: 445px;
    }
  }
  &__article {
    width: 334px;
  }
  &__title {
    margin-bottom: 16px;
    @include text($size: 40px, $font-weight: 600);
  }
  &__text {
    width: 162px;
    font-size: 14px;
    font-weight: 300;
    @media screen and (min-width: $tablet) {
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: 400;
    }
  }
}
